	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Just like Las Vegas","date":"02 Dec 2004","slug":"/2004-12-02-just-like-las-vegas/","tags":"Y2004"},"html":"<p><a title=\"inessential.com: Weblog: Comments for Black Box Parsing\" href=\"http://inessential.com/?comments=1&postid=2993\">inessential: Black Box Parsing</a> \"A black box is like Las Vegas: what happens in the box stays in the box.\"</p>"},"next":{"fields":{"title":"Cardboard Train","date":"01 Dec 2004","slug":"/2004-12-01-cardboard-train/"}},"previous":{"fields":{"title":"Kat Clock","date":"02 Dec 2004","slug":"/2004-12-02-kat-clock/"}}},{"node":{"fields":{"title":"Cardboard Train","date":"01 Dec 2004","slug":"/2004-12-01-cardboard-train/","tags":"Y2004"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/12/crh_boxes.jpg\" border=\"0\" height=\"240\" width=\"320\" alt=\"crh_boxes.jpg\" /></p>\n<p>Papa was late coming home this afternoon, so he had to build a cardboard train for Christopher to make up for it. That white styrofoam thing is supposed to be a smokestack.</p>"},"next":{"fields":{"title":"Citizenship and Dual Nationality","date":"29 Nov 2004","slug":"/2004-11-29-citizenship-and-dual-nationality/"}},"previous":{"fields":{"title":"Just like Las Vegas","date":"02 Dec 2004","slug":"/2004-12-02-just-like-las-vegas/"}}},{"node":{"fields":{"title":"Citizenship and Dual Nationality","date":"29 Nov 2004","slug":"/2004-11-29-citizenship-and-dual-nationality/","tags":"Y2004"},"html":"<p>Since I'm often asked about Christopher's citizenship status, here is a primer on <a href=\"http://www.usembassy.de/germany/dual_nationality.html\" title=\"Citizenship and Dual Nationality - United States Mission to Germany\">Citizenship and Dual Nationality</a> from the US Embassy. For Christopher: \"A child born to an American parent and a German parent acquires both American and German citizenship at birth, regardless of place of birth, if the parents satisfy the jus soli or jus sanguinis requirements of their respective countries...  Neither country requires a person born under these circumstances to choose between American and German citizenship, i.e., he/she may keep both citizenships his/her entire life.\"</p>\n<p>However, for his parents: \"Under German law, a person may not have more than one citizenship unless he/she was born with both... Thus, German law requires an American who becomes a German citizen through the <em>Einb&uuml;rgerung</em> process to formally renounce his/her American citizenship, and a German who becomes an American citizen to give up his/her German citizenship.\"</p>"},"next":{"fields":{"title":"Stillstand","date":"27 Nov 2004","slug":"/2004-11-27-stillstand/"}},"previous":{"fields":{"title":"Cardboard Train","date":"01 Dec 2004","slug":"/2004-12-01-cardboard-train/"}}}],"pathPrefix":"","first":false,"last":false,"index":374,"pageCount":1101}}

